:root{
    --primary-color: #fff;
    --secoundry-color: #202223;
    --text-color: #6D7175;
    --border-color: #DBE1E5;
    --primary-btn:#5454FC;
    --action-btns: #DBE1E5;
    --input-bg: #ffffff;
    --input-icon: #6D7175;
    --m-input-bg: #DBE1E5;
    --options-bg: #FFFFFF;
    --options-hover: #f5f5f5;
    --pleceholder:#000;
}

.dark-theme{
    --primary-color: #121212;;
    --secoundry-color: #FFF;
    --text-color: #F5F5F5;
    --border-color: #ffffff;
    --primary-btn:#5454FC;
    --action-btns: #ffffff;
    --input-bg: #000000;
    --input-icon: #ffffff;
    --m-input-bg: #000000;
    --options-hover: #3A3B3C;
    --pleceholder:#ffffff;
    --options-bg: #242526;
}

body{ 
    font-family: inherit;
}

.lbUser_header {
    margin-bottom: 15px;
}

.lbUserheader_top {
    border-bottom: 1px solid var(--border-color);
    margin-bottom: 20px;
    padding: 0 20px 20px;
}

.lbUser_profile {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.lb_backbutton {
    background: transparent;
    padding: 0;
    border: none;
    margin-right: 5px;
}

.lb_backbutton svg path {
    fill: var(--secoundry-color);
}

.lbUser_profileouter {
    border-radius: 100%;
    width: 48px;
    height: 48px;
    overflow: hidden;
    margin-right: 10px;
}

.lbUser_profileImage{
    width: 100%;
    max-width: 100%;
}

.lb_title {
    font-size: 16px;
    margin-bottom: 0;
    color: var(--secoundry-color);
}

.lbUser_descrioption {
    font-size: 14px;
    font-weight: 400;
    color: var(--text-color);
    margin-bottom: 0;
    position: relative;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    transition: all 1s;
}

.lbReadmore {
    float: right;
    font-size: 13px;
    font-weight: 500;
    color: #5454FC;
    line-height: 20px;
    background: var(--primary-color);
    padding: 0 10px;
    border: none;
    position: absolute;
    right: 0;
    bottom: 0;
    cursor: pointer;
}

.lbUser_actions {
    padding: 0 20px;
}

.lbUser_actions_inner{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.lbBrowse_product { 
    margin-right: 5px;
}

.lbSearch_product { 
    margin-left: 5px;
}

.lbBrowsebtn{
    width: 100%;
}

.lbBrowsebtn, .lbSearch_product {
    border: none;
    padding: 0 0 0 13px;
    height: 38px;
    background: var(--action-btns);
    font-size: 12px;
    font-weight: 500;
    color: #202223;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-radius: 4px;
}

.lbBrowse_product, .lbSearch_product {
    width: 50%;
    position: relative;
}

.lbPosts_Products {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
}

.lb_image_holder {
    width: 25%;
    padding: 5px;
}

.lb_PPimage {
    aspect-ratio: 1/1;
    -webkit-aspect-ratio: 1/1;
    object-fit: cover;
    max-width: 100%;
    width: 100%;
}

.lbbrowse_dropdown {
    /* display: none; */
    transition: all 0.6s;
    position: absolute;
    width: 100%;
    background: var(--options-bg);
    box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.lbbrowse_option {
    width: 100%;
    background: var(--options-bg);
    padding: 10px 15px;
    cursor:pointer;
    color: var(--text-color)
}

.lbbrowse_option:hover {
    background: var(--options-hover);
}

.lbSearch {
    position: absolute;
    width: 0;
    padding: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s;
}

.lbSearchbox {
    width: 0;
    border: none;
    padding: 0;
    transition: all 0.3s;
}

.lbSearchbox:focus-visible, 
.lbSearchbox:focus, 
.lbSearchbox:hover, 
.lbSearchbox:active {
    outline: none;
    box-shadow: none;
}

.lbSearchClose {
    position: absolute;
    display: flex;
    padding: 10px 15px;
    cursor: pointer;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
}

.lbSearchClose svg {
    width: 0;
    height: 0;
    transition: all 0.3s;
}

.lbSearch.active .lbSearchClose svg{
    width: 16px;
    height: 16px;
}

.lbSearch.active .lbSearchClose svg path {
    fill: var(--text-color)
}

.lbSearch.active {
    width: 100%;
    transition: all 0.3s;
}

.lbSearch.active .lbSearchbox {
    padding: 5px 15px;
    width: 100%;
    height: 38px;
    border: 1px solid var(--border-color);
    color: var(--secoundry-color);
    transition: all 0.3s;
}

.lbUser_descrioption.active {
    display: block;
    overflow: visible;
    transition: all 1s ease-in-out;
}


/* ==== MOBILE DEVICE CSS START ==== */

.lbUser_embed.mobile_view {
    position: relative;
    overflow: hidden;
    padding: 0 5px;
    overflow-y: scroll;
    max-height: 485px;
    margin-top: 10px;
}

.mobile_view .lbUserheader_top {
    padding: 0 0 20px;
}

.lbUser_embed.mobile_view .lbUser_actions {
    padding: 0;
}

.lbUser_embed.mobile_view .lb_image_holder{
    width:50%;
}

.lbUser_embed.mobile_view .lbPosts_Products {
    /* max-height: 285px;
    overflow-y: hidden; */
    margin: 0 -5px;
}

.lbUser_descrioption.active .lbReadmore {
    visibility: hidden;
}

.lbUser_descrioption.active .lbReadmore:before {
    position: absolute;
    content: "Read Less";
    width: auto;
    opacity: 1;
    visibility: visible;
    padding: 0 0 0 10px;
}

.lbUser_embed.mobile_view .lbbrowse_dropdown.active {
    position: absolute;
    bottom: 0;
    width: 100%;
    max-width: 100%;
    left: 0;
    padding: 20px 0;
    border-radius: 0 0 4px 4px;
    background: var(--primary-color);
}

.lbUser_embed.mobile_view .lbbrowse_dropdown {
    box-shadow: none;
}

.lbUser_embed.mobile_view .lbBrowse_overlay:after {
    position: absolute;
    content: "";
    background: rgba(0,0,0,0.3);
    width: 100%;
    height: 100%;
    bottom: 122px;
    left: 0;
}

.lbUser_embed.mobile_view .lbSearch_overlay .lbSearch.active {
    position: absolute;
    top: 40px;
}

.lbUser_embed.mobile_view.lbSearch_overlay:after {
    position: absolute;
    content: "";
    background: var(--primary-color);
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 1;
}

.lbUser_embed.mobile_view .lbSearch.active {
    position: absolute;
    top: 40px;
    z-index: 2;
    width: 100%;
    transition: none;
    padding: 0 10px;
}

.lbUser_embed.mobile_view .lbSearchbox {
    width: 100%;
    height: 38px;
    background: var( --m-input-bg);
    color: #fff;
    border: none;
    transition: none;
    border-radius: 3px;
}

.lbUser_embed.mobile_view .lbSearchClose {
    padding: 10px;
    right: 10px;
}

.lbUser_embed.mobile_view::-webkit-scrollbar {
    display: none;
}

/* ==== MOBILE DEVICE CSS END ==== */

/* DARK MODE THEME CSS START */

.preview_screen.dark-theme {
    background: #121212;
}


/* DARK MODE THEME CSS END */





@media screen and (max-width:767px){
    .lb_image_holder {
        width: 33.33%;
    }
}


@media screen and (max-width:575px){
    .lb_image_holder {
        width: 50%;
    }

    
}












