:root{
    --primary-color: #fff;
    --secoundry-color: #202223;
    --text-color: #6D7175;
    --border-color: #DBE1E5;
    --action-btns: #DBE1E5;
}

.dark-theme{
    --primary-color: #000;
    --secoundry-color: #FFF;
    --text-color: #F5F5F5;
    --border-color: #fff;
    --action-btns: #fff;
}

/* LIVE PREVIEW IFRAME CSS START */
.sob_mobile{
    padding-bottom: 205%;
    z-index: -1;
}
.sob_mobile iframe{
    position: absolute;
    top: -18%;
    left: -21%;
    width: 142%;
    height: 139%;
    transform: scale(0.7);
    border: 0;
    margin: 0;
    border-radius: 30px;
}

/* LIVE PREVIEW IFRAME CSS END */

/* LEFT SIDEBAR CSS START */
.shopon_bio .ec_sidebar_inner {
    overflow-y: auto;
}


.shopon_bio .ec_sidebar_inner::-webkit-scrollbar,
.desktoptab_inner .lbPosts_Products::-webkit-scrollbar{
    width: 3px;
}
   
.shopon_bio .ec_sidebar_inner::-webkit-scrollbar-track,
.desktoptab_inner .lbPosts_Products::-webkit-scrollbar-track {
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}
   
.shopon_bio .ec_sidebar_inner::-webkit-scrollbar-thumb,
.desktoptab_inner .lbPosts_Products::-webkit-scrollbar-thumb {
    background-color: darkgrey;
}
.desktoptab_inner {
    height: 100%;
}
.get_started{
    padding: 19px 0px 25px;
    border-top: 1px solid #DBE1E5;
}

.getStarted_inner {
    padding: 0 15px 12px;
    border-bottom: 1px solid #DBE1E5;
}

.get_started h6 {
    font-size: 13px;
    font-weight: 600;
    line-height: 18px;
    color: #202223;
    margin-bottom: 5px;
}

.get_started .getStarted_inner > p{
    font-size: 12px;
    font-weight: 400;
    color: #6D7175;
}

.linkin_preview .preview_screen .lbUser_embed {
    /* border: 1px solid #f1f1f1; */
    /* height: calc(100vh - 180px); */
}

.linkin_preview .preview_screen.dark-theme .lbUser_embed {
    border: none;
}

.preview_screen.dark-theme{
    background: none !important;
}

.shopOn_mobile .preview_screen.dark-theme {
    background: #121212 !important;
}

.upload_logo {
    margin-right: 15px;
}

.logo_area {
    position: relative;
    border-radius: 100%;
    width: 50px;
    height: 50px;
    background: #e4e6ee;
}

.noProfile_img {
    align-items: center;
    /* background-color: #e4e6ee; */
    border-radius: 3px;
    color: #2b2b2b;
    display: flex;
    flex: 0 0 42px;
    font-size: 23px;
    font-weight: 700;
    height: 50px;
    justify-content: center;
    margin-right: 10px;
    width: 50px;
}

.errorBorder {
    border: 1px solid #cf0b0b!important;
}
.logo_area img {
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    border-radius: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.add_logo {
    position: absolute;
    right: 0;
    bottom: 0;
    background: #5454fc;
    border-radius: 100%;
    z-index: 9;
    display: flex;
    align-items: center;
    width: 20px;
    height: 20px;
    border: none;
}

.add_logo svg path{
    fill: #fff;
}

.shopon_textarea {
    margin: 13px 0 11px;
}

.shopon_textarea textarea::-webkit-scrollbar{
    width: 5px;
}
   
.shopon_textarea textarea::-webkit-scrollbar-track {
box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3);
}
   
.shopon_textarea textarea::-webkit-scrollbar-thumb {
    background-color: darkgrey;
}

.text_limit {
    font-size: 12px;
    color: #6D7175;
    line-height: 17px;
    font-weight: 400;
    margin-top: 5px;
    text-align: right;
}

.nextTo_getStarted {
    padding: 0 15px;
}

.choose_username {
    margin-bottom: 30px;
}

.choose_username h6 {
    font-size: 13px;
    font-weight: 500;
    line-height: 17px;
    color: #6D7175;
    margin: 16px 0 12px;
}

.username_linkinbio .input-group-prepend {
    background: #F4F6F9;
}

.choose_username .input-group-text {
    font-size: 12px;
    font-weight: 500;
    line-height: 17px;
    color: #202223;
    padding: 10px 12px;
    border: 1px solid #C9CCCF;
}

.username_linkinbio input.shoponBio_input {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    padding: 10px 12px;
    height: 39px;
    border: 1px solid #C9CCCF;
    border-radius: 0 0.42rem 0.42rem 0 !important;
}

.verifyIcon_sb {
    position: absolute;
    top: 1px;
    right: 1px;
    width: 38px;
    display: block;
    background: #fff;
    padding: 11px 10px 10px !important;
    border-radius: 0 6px 6px 0;
}

.verifyIcon_sb  .availability_error, 
.verifyIcon_sb  .availability_error i {
    display: block;
    line-height: normal;
}

.verifyIcon_sb .input-group-text {
    padding: 0;
    line-height: normal;
    border: none !important;
}

.choose_username .input-group-text.availability_ {
    padding: 0;
    width: 18px;
    height: 20px;
    border: none !important;
}

.choose_username .username_linkinbio .verifyIcon_sb .availability_ {
    height: 15px;
}

.choose_username .input-group-text.availability_success i {
    line-height: normal;
}

.choose_username .input-group-text.availability_success i {
    color: #5454FC;
    z-index: 9;
}

.choose_username .input-group-text.availability_error i {
    color: #cf0b0b;
    z-index: 9;
}

.copyUrl_section {
    padding: 20px 28px;
    border-top: 1px solid #DBE1E5;
    border-bottom: 1px solid #DBE1E5;
    margin: 16px 0;
}

.copyUrl_box {
    display: flex;
    align-items: center;
    border: 1px solid #C9CCCF;
    border-right: none;
    padding: 9px 12px;
    width: calc(100% - 72px);
    border-radius: 3px 0 0 3px;
    background: #fff;
}

.copyUrl_box span {
    font-size: 14px;
    font-weight: 600;
    margin-right: 11px;
    white-space: nowrap;
    line-height: 17px;
}

.copyUrl_box a {
    font-size: 12px;
    font-weight: 500;
    color: #5254FD;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 18px;
    white-space: nowrap;
}

.copyUrl_button {
    border: 1px solid #C9CCCF;
    font-size: 12px;
    font-weight: 600;
    color: #333;
    padding: 9px 17px;
    border-radius: 0 3px 3px 0;
    background: #fff;
}

.themeSelect_sb {
    margin-top: 17px;
    /* padding-bottom: 272px; */
}

.setting_linkinbio h6 {
    padding: 0 28px;
    margin-bottom: 12px;
}

.theme_select {
    display: flex;
    align-items: center;
    justify-content: left;
}

.setting_linkinbio .theme_select li:first-child{
    margin-right: 12px;
}

.setting_linkinbio .theme_select li button {
    font-size: 12px;
    font-weight: 500;
    line-height: 17px;
    text-align: center;
    padding: 10px 14px;
    display: flex;
    align-items: center;
    border: none;
    border-radius: 4px;
}

.setting_linkinbio .theme_select li button svg {
    margin-right: 8px;
}

.themeSelect_sb .theme_select li button .theme_bg {
    width: 86px;
    height: 82px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.themeSelect_sb .theme_select li button .theme_bg.light_bg{
    background: #fff;
}

.themeSelect_sb .theme_select li button .theme_bg.dark_bg {
    background: #000;
}

.themeSelect_sb .small_heading {
    padding: 0;
    margin-bottom: 12px;
}

.setting_linkinbio .theme_select li button.active {
    background: #5975FF;
}

.setting_linkinbio .theme_select li button.active svg path {
    fill: #fff;
}

.setting_linkinbio .theme_select li button.active p{
    color:#fff;
}

.copyUrl_section.disabled .copyUrl_box, .copyUrl_section.disabled .copyUrl_button {
    pointer-events: none;
    opacity: 0.5;
}


/* LEFT SIDEBAR CSS END */


/* DESKTOP & MOBILE PREVIEW CSS START */

.linkin_preview .preview_tab {
    height: 100%;
    width: 100%;
}

.linkin_preview .preview_screen {
    max-width: 790px;
    padding: 0px 5px;
    background: var(--primary-color);
    margin: 0 auto;
}

.linkinbio_header {
    margin-bottom: 15px;
}

.linkin_head_desc {
    border-bottom: 1px solid var(--border-color);
    margin-bottom: 20px;
    padding: 0 20px 20px;
}

.linkinbio_profile{
    margin-bottom: 10px;
}

.back_btn_linkbio {
    background: transparent;
    padding: 0;
    outline: none;
    border: none;
    margin-right: 5px;
}

.back_btn_linkbio svg path {
    fill: var(--secoundry-color);
}

.linkinbio_logo {
    border-radius: 100%;
    width: 48px;
    height: 48px;
    overflow: hidden;
    margin-right: 10px;
}

.linkinbio_logo img{
    width:100%;
}

.bio_title {
    font-size: 16px;
    margin-bottom: 0;
    color: var(--secoundry-color);
}

.linkinbio_preview p {
    font-size: 14px;
    font-weight: 400;
    color: var(--text-color);
    margin-bottom: 0;
    position: relative;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.read_more_desc {
    float: right;
    font-size: 13px;
    font-weight: 500;
    color: #5454FC;
    line-height: 20px;
    background: #fff;
    padding: 0 0 0 9px;
    border: none;
    position: absolute;
    right: 20px;
    bottom: 0;
}

.action_buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
}

.action_buttons .left_align, 
.action_buttons .right_align {
    width: 50%;
}

.action_buttons .left_align {
    margin-right: 5px;
}

.action_buttons .right_align {
    margin-left: 5px;
}

.action_buttons .left_align button,
.action_buttons .right_align button {
    width: 100%;
    border: none;
    padding: 0;
    height: 38px;
    background: var(--action-btns);
    font-size: 12px;
    font-weight: 500;
    color:#202223;
}

.action_buttons .left_align button{
    padding: 0 0 0 13px;
}

.user_embed {
    padding: 0 20px;
}

.linkinbio_preview ul{
    padding: 0;
    margin: 0;
}

.linkinbio_preview ul:after {
    display: block;
    clear: both;
    content: "";
}

.linkin_preview .desktoptab_inner li {
    width: 50%;
    float: left;
    padding: 5px;
}

.linkinbio_preview li.image_holder img {
    aspect-ratio: 1/1;
    -webkit-aspect-ratio: 1/1;
    object-fit: cover;
}


/* -- MOBILE PREVIEW CSS START -- */

.linkin_preview .mobile_tab_inner {
    position: relative;
    z-index: 1;
}

.linkin_preview .mobile_tab_inner{
