.linkin_preview .mobile_tab_inner .preview_screen .lbUser_embed::-webkit-scrollbar{  
    display: none;
}

.linkinbio_mobile_preview .linkinbio_header {
    margin-bottom: 10px;
}

.linkinbio_mobile_preview .linkin_head_desc {
