@media screen and (max-width:1599px){
    .content_wrapper{
        height: auto;
    }

    .logo_area{
        width: 50px;
        height: 50px;
    }

    .add_logo{
        width: 20px;
        height: 20px;
    }

    .uploadLogo_outer .form-control {
        height: 35px;
        padding: 0.5rem 1rem;
    }

    .getStarted_inner {
        padding: 0 15px 12px;
        border-bottom: 1px solid #DBE1E5;
    }

    .nextTo_getStarted {
        padding: 0 15px;
    }

    .get_started h6{
        font-size: 14px;
    }

    .get_started .getStarted_inner > p {
        font-size: 11px;
    }

    .uploadLogo_outer input{
        font-size: 13px;
    }

    .shopon_textarea textarea.form-control {
        font-size: 13px;
    }

    .choose_username h6 {
        font-size: 12px;
    }

    .get_started {
        padding: 19px 0px 0;
    }

    .text_limit {
        font-size: 10px;
    }

    .choose_username {
        margin-bottom: 10px;
    }

    .copyUrl_section {
        padding: 10px 15px;
        margin-bottom: 0;
    }

    .linkin_preview .mobile_tab_inner .preview_screen {
        max-width: 260px;
        min-height: 500px;
    }

    .linkin_preview .mobile_tab_inner .preview_screen .lbUser_embed {
        max-height: 470px;
        max-width: 270px;
    }

}

